@use 'base/variables' as v;
@use 'base/mixins' as m;

.lineup{
    background-color: v.$violeta;
    color:v.$blanco;
    padding: 3rem 0;
    margin: 2rem 0;
    h3{
        font-size: 4.5rem;
    }
    h3,p{
        text-align: center;
        padding: 1rem 0;
    }
    @include m.tablet{
        h3{
            font-size: 3rem;
        }
    }
    .dia{
        text-transform: uppercase;
        font-size: 1.8rem;

        @include m.tablet{
            
            font-size: 1.5rem;
            
        }    
        
    }
}
.fondos{
    width: 100%;
    height: 100%;
    margin: 0 auto;
    img{
        width: 100%;
        height: 100%;
    }
}
.escenario-contenedor{
    @include m.grid(2, 2rem); 
    margin: 0 auto;
    width: 90%;
    .evento{
        font-size: 2rem;
    }
    @include m.tablet{
        @include m.grid(1 , 2rem);
}

.programacion{
    @include m.resetear-lista;
    p{
        font-size: 1.8rem;
        text-transform: uppercase;
    }
    li{
        font-size: 1.5rem;
        line-height: 3rem;
        padding: 0 2rem;
        font-weight: 700;
            span{
                font-weight: 400;
            } 
            
           
      }
      
      li:last-child{
        padding-bottom: 2rem;
        }
      
      @include m.tablet{
        p{
            font-size: 1.5rem;   
        }
        li{
            font-size: 1.2rem;
            line-height: 2.2rem;
            li:last-child{
                padding-bottom: 2rem;
            }
        }
        
      }
        
    }
}

.bg-naranjo{
    background-color: v.$naranjo;
    
        &:hover{
            transform: scale(1.1);
            transition: all ease-in 0.5s;
        }
   
}
.bg-crema{
    background-color: #EBD8B2;
    color: v.$negro;
}
.bg-marron{
    background-color: #5F264A;
}
.programacion{
    line-height: 2.5rem;
}