@use 'base/variables' as v;
@use 'base/mixins' as m;

.sobre-festival{
    margin-bottom: 5rem;
   @include m.grid(2,5rem);

    @include m.tablet{
        @include m.grid(1,3rem);
    }
}
.imagen{
    img{
        width: 100%;
    }
}
.texto{
    text-align: center;
    h2{
        font-size: 5rem;
        text-transform: uppercase;
        color:v.$naranjo;
        margin-bottom: 1rem;
        span{
            color: v.$violeta;
        }
        @include m.tablet{
            font-size: 3.5rem;
            
        }
    }
    .fecha{
        font-size: 2.2rem;
        background-color: v.$naranjo;
        color: v.$blanco;
        border-radius: 10px;
        padding: 1rem;
        font-weight: 700;
        @include m.tablet{
            font-size: 1.8rem;
            padding: 0.5rem ;
        }
    }
    p{
        font-size: 2rem;
        line-height: 2rem;
        @include m.tablet{
            font-size: 1.5rem;
        }
    }
}