@use 'variables' as v;

@mixin telefono{
    @media (max-width:v.$telefono){
        @content;
    }
}
@mixin tablet{
    @media (max-width:v.$tablet){
        @content;
    }
}
@mixin desktop{
    @media (max-width:v.$desktop){
        @content;
    }
}
@mixin desktopHD{
    @media (max-width:v.$desktopHD){
        @content;
    }
}
@mixin grid($columnas, $espacio){
    display:grid;
    grid-template-columns:repeat($columnas, 1fr);
    gap:$espacio;
}

@mixin resetear-lista{
    margin:0;
    padding: 0;
    list-style: none;
}