@use 'base/variables' as v;
@use 'base/mixins' as m;

// header
.header{
     background-color: v.$violeta;
     height: auto;   
}
.fijo{
     position: fixed;
     width: 100%;
     height: 100px;
     box-shadow: .1rem .2rem .3rem v.$negro;
          @include m.tablet{
               height: 160px;
          }
}
.header_body{
     display: flex;
     justify-content: space-between;
     align-items: center;
     padding: 0 2rem;
     // logo
     h1{
          color: v.$naranjo;
          text-transform: uppercase;
          font-weight: bold;
          span{
               
               color: v.$blanco;
          }
     }
     //mobile
     @include m.tablet{
          display: block;
          text-align: center;
               h1{
                    margin: 0;
                    padding:2rem;
                    font-size: 3rem;
               }
     }
   
}
// navegacion
.navegacion_principal{
    
     ul{
          display: flex;
          align-items: center;
          padding:0;
          //mobile      
          @include m.tablet{
               padding-bottom:1.5rem;
               flex-direction: column;
               text-align: center;
               margin: 0;
               gap: 1rem;
          }
     
               li{
                    list-style: none;
                    &:hover{
                         cursor:pointer;
                         transform: scale(1.2);
                         transition: all 0.5s ease-in-out;
                         
                    }
                    a{
                         color: v.$blanco;
                         padding: 1rem;
                         text-decoration: none;
                         &:hover{
                              color: v.$naranjo;
                         }
                         //mobile
                         @include m.tablet{
                              padding:0;
                              font-size: 1.5rem;
                         }
                    }
               }
     } 
}


