@use 'base/variables' as v;
@use 'base/mixins' as m;

.boletos{
    margin-bottom: 5rem;
    .texto-boletos{
        text-align: center;
        color: v.$naranjo;
        font-size: 3rem;
        text-transform: uppercase;
        padding:3rem 0;
        
       
    }
    .pase-box{
        @include m.grid(3, 2.5rem); 
        height: auto;
        @include m.tablet{
            @include m.grid(1,2rem);
        }
    }
}
.pase{
    color: v.$blanco;
    .titulo{
        text-align: center;
        font-size: 1.5rem;
        text-transform: uppercase;
        background-color: v.$violeta;
        margin: 0;
        padding: 1rem;
    }
    &-header{
        background-color:v.$naranjo;
        color: v.$blanco;
        text-align: center;
        padding: .5rem;
           p{
            font-size: 1.5rem;
            text-transform: uppercase;
            }
        }
    &-body{
        background-color: v.$violeta;
            padding: 2rem;
            ul{
                @include m.resetear-lista;
                line-height: 3rem;
                text-transform: uppercase;
                li{
                    border-bottom:  1px solid v.$blanco;
                    padding: .5rem;
                }
            }
        }
    &-footer{
        text-align: center;
        background-color: v.$naranjo;
        padding:1rem;
        p{
            font-size:1.5rem;
        }
    }
}


