@use 'variables' as v;

// globales
html{
    font-size: 62.5%;
    box-sizing: border-box;
}
*,
*:before,
*:after{
    box-sizing: inherit;
}
body{
    font-family: v.$fuente_principal;
    font-size: 1.6rem;  
    &.fijar-body{
        overflow: hidden;  
    }
}
.contenedor{
    max-width: 120rem;
    width: 90%;
    margin: 0 auto;
}

p{
    font-size: 1.2rem;
}
h1{
    font-size: 4.5rem;
}