@use 'base/variables' as v;
@use 'base/mixins' as m;

.galeria{
    padding: 5rem 0;
    h3{
        text-transform: uppercase;
        font-size: 3rem;
        text-align: center;
        color: v.$naranjo;
    }
}
.galeria-imagenes{
    @include m.grid(6, 2rem);
    @include m.resetear-lista;
    
    @include m.desktop{
        @include m.grid(3, 2rem);
    }
    @include m.tablet{
        @include m.grid(2, 2rem);
    }
    
    img{
        width: 100%;
        height: 100%;

    }
    
}
.overlay{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    width: 100%;
    height: 100%;

    display: grid;
    place-items: center;
    background-color: rgb(0 0 0/.5);
    picture{
        display: block;
        max-width: 100rem;
        img{
            width: 100rem;
            height: 80rem;
            padding:0 2rem;
            @include m.desktopHD{
                width: 100%;
                height: 50rem;
            }
            @include m.desktop{
                
                height: 50rem;
            }
            @include m.tablet{

                height: 30rem;
            }
        }
    }
    
}
.btn-cierre{
    background-color: v.$naranjo;
    color:v.$blanco;
    padding: 1rem;
    border-radius: 50%;
    text-align: center;
    font-size: 2rem;
    text-transform: uppercase;
    
}