@use 'base/variables' as v;
@use 'base/mixins' as m;

.video{
    position: relative;
    max-height: 70rem;
    overflow: hidden;
    margin-bottom: 5rem;

    .overlay{
        position: absolute;
        background: rgb(37, 23, 73);
        background: linear-gradient(270deg, rgba(37, 23, 73,0.5) 0%, rgba(37, 23, 73,0.2) 100%);
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    video{
        width: 100%;
        height:100%;
        display: block;
        @include m.tablet{
            width:auto;
            
        }
    }
}
.video_body{
    margin-left:10rem;
    z-index: 10;
        @include m.tablet{
            margin-left:2rem;
        }
            &:hover{
            cursor:pointer;
            transform: scale(1.1);
            transition: all 0.5s ease-in;
        }
    h2{
        color:v.$naranjo;
        font-size: 7rem;
        margin: 0;

        span{
            color: v.$blanco;
        }
    }
    p{
        display: inline-block;
        color:v.$blanco;
        background-color: v.$naranjo;
        text-align: center;
        padding: 1.2rem;
        border-radius: 10px;           
    }
    @include m.tablet{      
        h2{
            font-size: 4rem;
            
        }
        p{
            font-size: 1.5rem;
            padding: .8rem;
            
        }
    }
}